@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";


*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  color: #2B2A2C;
  font-weight: 400;
}

.no-scroll {
    overflow: hidden;
}

a { 
  text-decoration: none;
  color: inherit;
}

.btn {
    font-family: 'Montserrat', sans-serif;
    font-size: 1rem;
    color: #6A696B;
    text-transform: uppercase;
    border: none;
    background-color: #fff;
    border: 1px solid #6A696B;
    border-radius: 30px;
    padding: 1rem 1.8rem;
    cursor: pointer;
    transition: 0.2s linear;
        &:focus {
        outline: none;
        }
        &:hover {
            color: #fff;
            background-color: #6A696B;
            box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
        }
}

// .container {
//     width: 100%;
//     min-width: 77rem;
//     padding: 0 1rem;
//     margin: 0 auto;
// }

@media screen and (min-width: 77rem) {
    .container {
        width: 100%;
        min-width: 76rem;
        padding: 0 1rem;
        margin: 0 auto;
    } 
}



// Header
.header {
    background-color: white;
    padding-top: 1.125rem;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .logo {
            display: flex;
            justify-content: start;
            align-items: center;      
            .photo {
                width: 57px;
                height: 57px;
                img {
                    border-radius: 50%;
                }
            }
            .text {
                padding-left: 8px;
                .name {
                    font-size: 1rem;
                    color: black;
                    font-weight: 700;
                }
                .prof {
                    color: black;
                    font-size: 0.75rem;
                    font-weight: 400;
                    opacity: 70%;
                }
            }
        }
        .nav {
            .link {
                
                color: black;
                font-size: 0.875rem;
                font-weight: 700;
                text-transform: uppercase;
                line-height: 4;
                margin-right: 2.5rem;
                position: relative;
                 &::after {
                     content: '';
                     position: absolute;                   
                     width: 0;
                     height: 2px;
                     top: 18px;
                     left: 3px;
                     background-color: black;                    
                     transition:  0.2s linear;                  
                 }               
            }
            .link:hover::after,
            .link:focus::after,
            .active {   
                width: 90%;
            }
            .link-btn {
                color: black;
                font-size: 0.875rem;
                font-weight: 700;
                text-transform: uppercase;
                margin-top: -10px;
                border: 1px solid black;
                padding: 10px 33px;
                border-radius: 20px;
                transition: 0.2s linear;

                &:hover {
                    color: white;
                    background-color: black;
                }
            }
        }
    }
}

// Main
.main {
    margin-top: 15px;
    border-bottom: 1px solid $lite;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__about {
        text-transform: none;

        .hello {
            font-size: 2rem;
            font-weight: 200;
            color: black;
        }
        .name {
            font-size: 2.8rem;
            font-weight: 700;
            color: black;
        }
        .prof {
           font-size: 1.125rem;
           font-weight: 400;
            opacity: 0.7;
        }
        .social {
            display: flex;
            margin-top: 20px;
            margin-bottom: 40px;
            .icon {
                transition: 0.2s linear;
            }
            .icon:hover {
                transform: translateY(-5px);
            }
            .icon i {
                font-size: 2.5rem;
                padding-right: 10px;   
            }
            .be {
                color: #256AEC;
            }
            .dr {
                color: #E51A68;
            }
            .in {
                color: #007BB6;
            }
            .fb {
                color: #3A5898;
            }
            .inst {
              color: #13578A;  
            }
            .tw {
                color: #56ACEF; 
            }
        }
        .btn {
            margin-right: 1rem;
        } 
    }
    &__photo {
        background-image: url(../img/symbols.png);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;       
    }
}

// Works
.works {
    margin-top: 2.5rem;
    &__menu {
        display: flex;
        justify-content: center;
        .link {
            padding-right: 2rem;
            color: rgba(0, 0, 0, 0.7);
            font-size: 0.75rem;
            font-weight: 400;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &__cards {
        margin-top: 1.87rem;
        margin-left: -1rem;
        margin-right: -1rem;
        display: flex;
        // flex-wrap: wrap;  
    }
    &__card {
        width: 100%;
        max-width: 370px;
        height: auto;  
        border: 1px solid $lite;
        border-radius: 10px;
        overflow: hidden;
        box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
        margin-left: 1rem;
        margin-right: 1rem;
        margin-bottom: 1rem;
        transition: 0.2s linear;
        .photo {
            display: block;
            width: 100%;
            height: auto;
        }
        .category {
            font-size: 0.75rem;
            text-transform: uppercase;
            padding-top: 14px;
            padding-left: 14px;
        }
        &:hover {
            box-shadow: 0 0 4px rgba(0, 0, 0, 0.9);
            cursor: pointer; 
        }
    }
    &__name {
        display: flex;
        justify-content: space-between;
        padding: 8px 14px 14px 14px;
        .title {
            text-transform: uppercase;
            font-weight: 700;
        }
        .date {
            text-transform: uppercase;
            font-weight: 700;  
        }
    }
}
.works-btn {
    display: block;
    margin: 2.6rem auto;
    padding: 0.62rem 1.56rem ;      
}

// About
.about {
    &__items {
        margin-top: 7rem;
        margin-bottom: 150px;
        width: 100%;
        max-width: 68rem;
        height: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__photo {
        width: 100%;
        max-width: 47%;
        position: relative;
        img {
            width: 100%;
            max-width: 540px;
            height: auto;
            position: relative;
        }
        &:before {
            content: '';
            width: 414px;
            height: 414px;
            background-color: $lite;
            position: absolute;
            bottom: 0;
            left: 1.2rem;
        }      
    }
    &__content {
        width: 100%;
        max-width: 50%;
        height: auto;
 
        .title {
          font-size: 2.8rem;
          font-weight: 700;  
        }
        .subtitle {
            font-size: 5.5rem;
            letter-spacing: -3px;
            font-weight: 700;
            opacity: 15%;
        }
        .text {
            font-size: 0.75rem;
            font-weight: 300; 
        }
        .btn-about {
            margin-top: 2rem;
            margin-right: 1rem;
        }
    }
}

// Rewiews
.rewiews {
    background-color: #332F30;
    background-image: url(../img/Symbol-bg.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
    &__items {
        width: 100%;
        max-width: 830px;
        margin: 0 auto;
        position: relative;    
    }
    &__text {
        font-size: 0.875rem;
        font-weight: 400;
        color: $white;
        line-height: 2rem;
        text-align: center;
        padding:  4.25rem 0.5rem;       
    }
    &__author {
        text-align: center;
        position: absolute;
        top: calc(100% - 51px);
        left: calc(50% - 75px );
    
        .photo {
            width: 103px;
            height: 103px;
            border-radius: 50%;
            border: 4px solid $lite;
            margin: 0 auto;   
        }
        img {
            display: block;
            width: 103px;
            height: 103px;
        }  
        .name {
            font-size: 1rem;
            font-weight: 700;
            color: $black;
            padding-top: 0.5rem;
            padding-bottom: 0.5rem;    
        }
        .from {
            color: #6A696B;
            font-style: italic;
            font-size: 0.875;
            font-weight: 200;
            padding-bottom: 0.5rem;
        }
        .stars i {
            font-size: 0.875rem;
            color: $black;
        }
    }
}

// Read me
.read {
    margin-top: 12rem;
    margin-bottom: 9rem;
    &__header {
        text-align: center;
       .title {
        font-size: 2.8rem;
        font-weight: 700;
        text-transform: uppercase;
        color: $black;
        padding-bottom: 0.5rem;
        }
        .subtitle {
        font-size: 1.125rem;
        font-weight: 300;
        color: $gray;  
        } 
    }
    &__items {
        display: flex;
        justify-content: space-between;
        padding-top: 3rem;
    }
    &__item {
        margin-bottom: 2rem;
        width: 100%;
        max-width: 370px;
        height: auto;
        padding: 0 0.5rem;
        .photo {
            position: relative;
            width: 100%;
            max-width: 370px;
            height: auto;
            img {
                display: block;
                width: 100%;
                max-width: 370px;
                height: auto;  
            }
            &::after {
                content: '';
                position: absolute;
                display: block;
                width: 77px;
                height: 77px;
                background-color: $black;
                top: 0;
            }
            .date {
                position: absolute;
                top:20px;
                left: 20px;
                z-index: 3;
                .day {
                    color: $white;
                    font-size: 1rem;
                    font-weight: 400;
                    padding-left: 8px;
                }
                .month {
                    color: $white;
                    font-size: 1rem;
                    font-weight: 400;  
                }
            }       
        }
        .title {
            font-size: 1.125rem;
            font-weight: 700;
            color: $black;
            text-transform: uppercase;
            letter-spacing: 0.1rem;
            padding-top: 1rem;
        }
        .category {
            font-size: 0.75rem;
            font-weight: 400;
            color: $black; 
            letter-spacing: 0.1rem;
            padding-top: 0.5rem; 
        }
        .text {
            font-size: 0.75rem;
            font-weight: 300;
            color: $black;
            line-height: 1.2rem; 
            letter-spacing: 0.1rem;
            padding-top: 0.2rem;
            padding-bottom: 2.5rem;
        }
    }
    &__more {
        text-align: right;
        padding-right: 1rem;
        a {
            font-size: 0.875rem;
            font-weight: 400;
            color: $gray;
        }
        &:hover {
            text-decoration: underline;
        }      
    }
}    
      

// Fooer
.footer {
    width: 100%;
    height: auto;
    background-color: $black;
    color: #8D8D8E;
    &__content {
        display: flex;
        justify-content: space-between;
        padding: 0.3rem 0;
    }
    &__item {
        width: 100%;
        max-width: 570px;
        height: auto;
        display: flex;
        justify-content: start;
        align-items: center;
    }
    &__label {
        padding-right: 3rem;
        font-size: 0.875rem;
        font-weight: 400;
    }
    &__social {       
        i {
            color: #8D8D8E;
            font-size: 1.3rem;
            font-weight: 400;
            padding-left: 1rem;
            transition: 0.2s linear;
            &:hover {
                color: white;
            }
        }    
    }
    &__menu {       
        a {
            color: #8D8D8E;
            padding-left: 1.5rem;
            transition: 0.2s linear;    
        }
        .link:hover {
            color: white;
        }
        .btn {
            margin-left: 1.5rem;
            font-size: 1rem;
            color: #6A696B;
            border: none;
            background-color: $black;
            border: 1px solid #6A696B;
            border-radius: 30px;
            padding: 0.5rem 1.2rem;
            cursor: pointer;
            transition: 0.2s linear;
                &:focus {
                outline: none;
                }
                &:hover {
                    color: #fff;
                    background-color: #6A696B;    
                }
        }       
    }
}

// Modals
.modal {
    display: flex;
    width: 100%;
    height: 100%;
    background-color: rgba(7, 4, 27, 0.8);
    top: 0;
    left:0;
    padding: 0 1rem;
    position: fixed;
    overflow: auto;
    &__dialog {
        width: 100%;
        max-width: 1200px;
        height: auto;
        margin: auto;
        background-color: $white;
        
    }
    &__work {
        width: 100%;
        height: auto;
        display: flex;
        justify-content: start;
        position: relative;
    }

    &__close {
        position: absolute;
        right: 10px;
        top: 10px;
        z-index: 3;
        cursor: pointer;
        transition: 0.2s linear;
        &:hover {
            transform: rotate(45deg);
        }
        i {
            font-size: 2rem;
            font-weight: 400;
            color: $black;
        }
        
    }
    &__previews {
        width: 65%;
        overflow: hidden;
        position: relative;
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 1;
            transform: translate3d(-50%, -50%, 0);           
        }
    }

    &__item {
        width: 35%;   
    }
  
    &__content {
        max-width: 288px;
        height: auto;
        padding-top: 2.875rem;
        padding-left: 1.625rem;
        .title {
            font-size: 2rem;
            font-weight: 700;
            color: $black;
            text-transform: uppercase;
            padding-top: 1rem;
            padding-bottom: 0.3rem;
        }
        .category {
            font-size: 1.125rem;
            font-weight: 400;
            color: $gray;
            text-transform: uppercase;
            padding-bottom: 2rem;
            .bord {
                padding-left: 1.6Ð°rem;
                padding-right: 0.8rem;
            }
        }
        .client {
            font-size: 1rem;
            font-weight: 700;
            color: $black;  
        }
        .from {
            font-size: 1rem;
            font-weight: 400;
            color: $gray;
            font-style: italic;
            padding-bottom: 2rem; 
        }
        .text {
            font-size: 0.75rem;
            font-weight: 300;
            color: $gray;
            line-height: 1.5rem;
             
        }
    }
    &__footer {
        display: flex;
        justify-content: space-between;
    }
    &__btn {
        border: none;
        background: none;
        cursor: pointer;
        padding: 1rem;
        color: $black;
        font-size: 0.875rem;
        font-weight: 400;
        &:focus {
           outline: none; 
        }
        &:hover {
            i {
                padding: 0 0.25rem;
            }
        }
        i {
            padding: 0.5rem;  
        }
    }   
 }

//  Modal resume
.modal {
    &__list {
        position: relative;    
    }
    &__resume {
        width: 100%;
    }
    &__list {
        width: 100%;
        max-width: 1200px;
        height: auto;
        margin: auto;
        background-color: $white;
        
        .title {
            text-align: center;
            font-size: 2.8rem;
            font-weight: 700;
            color: $black;
            text-transform: uppercase;
            padding-top: 5rem;
        }
        .btns {
            text-align: center;
            .btn--small {
                margin:  3.5rem 0.4rem;
                padding:0.25rem 1.5rem ;
                text-transform: uppercase;
            }
        }
        .btn--resume {
            display: block;
            background-color: $black;
            margin: 4rem auto;
            padding: 1rem 3.3rem;
            color: $white;
            &:hover {
                color: $black;
                background-color: $white;
            }
        }
    }
    &__resume--content {
        display: flex;
        justify-content: center;
        margin-top: 1rem;
    }
    &__experience {
        text-align: end;
        padding-right: 3rem;
        border-right: 1px solid $gray;

    }
    &__education {
        padding-left: 3rem;
    }
    &__col {
        width: 100%;
        max-width: 338px;
        .title--col{
           text-transform: uppercase;
           font-size: 1.125rem;
           font-weight: 700; 
        }
    }
    &__place {
        padding-top: 2.2rem;
        .year {
            font-size: 0.875rem;
            font-weight: 400;
            color: $black;
            padding-bottom: 0.75rem;
            text-decoration: underline;
        }
        .present {
            font-weight: 700;  
        }
        .were {
           font-size: 1rem;
           font-weight: 700; 
        }
        .prof {
            font-size: 0.875rem;
            font-weight: 400;
            color: $black;
            padding-top: 0.2rem;
            padding-bottom: 0.5rem;    
        }
        .descr {
            font-size: 0.75rem;
            font-weight: 300;
            color: $gray;
            line-height: 1.1rem;
        }
    }
    &__skills {
        .title--skills {
            padding-top: 1.8rem;
            padding-bottom: 1.6rem;
        }
    }
    &__skills--bar {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    &__content--bar {
        width: 50%;
        height: auto;
        .app {
            font-size: 0.875rem;
            font-weight: 300;
            line-height: 1.5rem;
         }      
    }         
    &__progress--bar {
        width: 139px;
        .element {
            width: 100%;
            height: 6px;
            background-color: $lite;
            .indic {
                background-color: $gray;
                height: 100%;
                transition: width 0.2s linear ;
            }            
        }
    }  
    &__awards {
        .title--awards {
            padding-top: 1.8rem;
            color: $black;
        }    
    }
    &__award {
        position: relative;
        padding-left: 50px;
        margin-top: 2rem;
        .year {
            position: absolute;
            font-size: 0.875rem;
            font-weight: 400;
            text-decoration: underline;
            top: 0;
            left: -1px;
        }
        .award-title {
            text-transform: uppercase;
            font-size: 1rem;
            font-weight: 700;
            padding-bottom: 0.3rem;
        }
        .award-from {
            font-style: italic;
            font-size: 0.875rem;
        }
        .descr {
            font-size: 0.75rem;
            font-weight: 300;
            color: #6A696B;
            line-height: 1.1rem; 
        }
    }   
}

// Modal contact
.modal {
    &__contact {
        width: 100%;
        max-width: 900px;
    }
    &__work--contact {
        justify-content: space-between;
    }
    &__talk {
        width: 100%;
        max-width: 430px;
        margin: 2rem;
        .title {
            font-size: 2.81rem;
            font-weight: 700;
            text-transform: uppercase;
            color: $black;    
        }
        .title--descr {
           font-size: 1rem;
           font-weight: 400;
           text-transform: uppercase;
           padding-bottom: 1.7rem; 
        }  
    }
    &__phone {
        padding-top: 0.5rem;
        display: flex;
        justify-content: start;
        align-items: center;
        .icon--phone {
            font-size: 1.1rem;
            color: $black;
            font-weight: 400;
            padding-right: 0.75rem;    
        }
        .number--phone {
            font-size: 0.875rem;
            font-weight: 300;
        }
    }
    &__form--mail {
       padding-top: 2.7rem;
       display: flex;
       flex-direction: column;
       .lable {
          font-size: 0.75rem;
          font-weight: 700;
          color: $black;
          padding-bottom: 0.5rem;
          padding-left: 1rem;
       }
       .input {
           display: block;
           width: 100%;
           font-family: inherit;
           font-size: 0.75rem;
            font-weight: 300;
            color: $black;
            padding: 1.25rem 1.75rem;
            border: 1px solid rgba(112, 112, 112, 0.3);
            
            border-radius: 30px;

            &:focus {
                outline: none;
            }
       } 
    }
    &__form--request {
        .textarea {
            resize: none;
            height: 200px;
        }   
    }
    &__form--btn {
        text-align: right;
        margin-top: 2rem;
        .btn-form {
            padding: 1rem 3.9rem;
        }     
    }
    &__previews--contact {
        width: 100%;
        max-width: 470px;
    }
}